.constructor {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 1000px;
  margin: 0 auto;

  &__title {
    width: 100%;
    text-align: center;
  }

  &__settings {
    width: 45%;
  }

  &__type {
    margin-bottom: 20px;

    &-list {
      display: flex;
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &-item {
      margin-right: 15px;

      & input {
        display: none;

        &:checked ~ label {
          border: 1px solid #000;
        }
      }

      & label {
        display: block;
        font-size: 0;
        line-height: 0;
        width: 116px;
        height: 78px;
        border: 1px solid #ccc;

        &[for=together] {
          background: url('/img/detail-type1.jpg') center no-repeat;
        }
        &[for=apart] {
          background: url('/img/detail-type2.jpg') center no-repeat;
        }
      }
    }
  }

  &__value {
    position: relative;
    padding-top: 20px;

    &-input {
      font-size: 16px;
      line-height: 1.4em;
      padding: 10px 15px;

      &:focus {

        & ~ label {
          top: 0;
          left: 0;
        }
      }
    }

    &-label {
      position: absolute;
      top: 36px;
      left: 16px;
      font-size: 14px;
      line-height: 16px;
      cursor: text;
      transition: top 0.4s, left 0.4s;

      &--top {
        top: 0;
        left: 0;
      }
    }
  }

  &__font {

    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
    }

    &-item {

      & input {
        display: none;

        &:checked ~ label {
          border: 1px solid #000;
        }
      }

      & label {
        display: block;
        width: 80px;
        font-size: 60px;
        line-height: 1.2em;
        text-transform: uppercase;
        text-align: center;


        &[for=zapf] {
          font-family: 'ZapfChanceCItalic';
        }

        &[for=nautilus] {
          font-family: 'NautilusPompilius';
        }

        &[for=nexa] {
          font-family: 'NexaScript-Heavy';
        }
      }
    }
  }

  &__material {
    margin: 20px 0;

    &-list {
      display: flex;
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &-item {

      & input {
        display: none;

        &:checked ~ label {
          color: #fff;
          background-color: #000;
        }
      }

      & label {
        display: block;
        margin: 0;
        padding: 10px 15px;
        color: #000;
        background-color: #fff;
        border: 1px solid #000;
        border-radius: 20px;
        margin-right: 15px;
      }
    }
  }

  &__gold-type {
    margin: 20px 0;

    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &-item {
      margin-bottom: 10px;

      & input {
        display: none;

        &:checked ~ label {

          &:after {
            position: absolute;
            content: '';
            left: 1.5px;
            top: 50%;
            margin-top: -14px;
            width: 25px;
            height: 25px;
            border: 1px solid #000;
            border-radius: 50%;
          }
        }
      }

      & label {
        display: block;
        position: relative;
        font-size: 18px;
        line-height: 1.5em;
        padding-left: 45px;

        &[for='yellow'] {

          &:before {
            background-color: #d1b574;
          }
        }

        &:before {
          position: absolute;
          content: '';
          top: 50%;
          margin-top: -10px;
          left: 5px;
          width: 20px;
          height: 20px;
          background-color: #cecece;
          border-radius: 50%;
        }
      }
    }
  }

  &__covering {
    margin: 20px 0;

    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &-item {
      margin-bottom: 10px;

      & input {
        display: none;

        &:checked ~ label {

          &:after {
            position: absolute;
            content: '';
            left: 1.5px;
            top: 50%;
            margin-top: -14px;
            width: 25px;
            height: 25px;
            border: 1px solid #000;
            border-radius: 50%;
          }
        }
      }

      & label {
        display: block;
        position: relative;
        font-size: 18px;
        line-height: 1.5em;
        padding-left: 45px;

        &[for='golden'] {

          &:before {
            background-color: #d1b574;
          }
        }

        &:before {
          position: absolute;
          content: '';
          top: 50%;
          margin-top: -10px;
          left: 5px;
          width: 20px;
          height: 20px;
          background-color: #cecece;
          border-radius: 50%;
        }
      }
    }
  }

  &__result {
    width: 45%;

    &-title {
      text-align: center;
      width: 100%;
    }

    &-together {
      display: flex;
      flex-wrap: wrap;

      &-container {
        display: inline-block;
        position: relative;
        height: 140px;
        margin: auto;
        padding: 0 43px;
      }
    }

    &-image {
      position: absolute;
      left: 0;
      top: 0;
      width: 43px;

      &--right {
        left: initial;
        right: 0;
        transform: scale(-1, 1);;
      }

      &--top {
        position: initial;
        width: 100%;
        transform: rotate(1.5deg);

        & img {
          width: 100%;
          height: auto;
        }
      }
    }

    &-string {
      font-size: 46px;
      margin-top: 89px;
      color: #aaa;

      &--zapf {
        font-family: 'ZapfChanceCItalic';
      }

      &--nautilus {
        font-family: 'NautilusPompilius';
      }

      &--nexa {
        font-family: 'NexaScript-Heavy';
      }

      &--gold {
        color: #d1c378;
      }
    }

    &-apart {

      &-container {
        width: 400px;
        margin: 0 auto;
      }

      &-list {
        display: flex;
        width: 80%;
        justify-content: space-around;
        margin: 0 auto;
        margin-top: -3px;
        padding: 0;
        list-style: none;
      }

      &-item {
        position: relative;
        padding-top: 7px;
        width: 30px;
        height: 30px;
        font-size: 30px;
        line-height: 1em;
        text-align: center;
        color: #aaa;
        vertical-align: top;
        text-transform: uppercase;

        &:before {
          position: absolute;
          content: '';
          top: 0px;
          left: 50%;
          width: 10px;
          height: 10px;
          background: url('/img/image_necklace-apart-silver-part.png') center no-repeat;
          background-size: cover;
        }

        &--zapf {
          font-family: 'ZapfChanceCItalic';
        }

        &--nautilus {
          font-family: 'NautilusPompilius';
        }

        &--nexa {
          font-family: 'NexaScript-Heavy';
        }

        &--gold {
          color: #d1c378;

          &:before {
            background: url('/img/image_necklace-apart-gold-part.png') center no-repeat;
            background-size: cover;
          }
        }

        &--top2 {
          margin-top: -2px;
        }

        &--top3 {
          margin-top: -3px;
        }

        &--top5 {
          margin-top: -5px;
        }
        &--top4 {
          margin-top: -4px;
        }

        &--top8 {
          margin-top: -8px;
        }

        &--top10 {
          margin-top: -10px;
        }
        &--top11 {
          margin-top: -11px;
        }

        &--top13 {
          margin-top: -13px;
        }

        &--top14 {
          margin-top: -14px;
        }

        &--top15 {
          margin-top: -15px;
        }

        &--top19 {
          margin-top: -19px;
        }

        &--top21 {
          margin-top: -21px;
        }

        &--top22 {
          margin-top: -22px;
        }
      }
    }
  }
}
