@font-face {
  font-family: "ZapfChanceCItalic";
  src: url("/fonts/ofontruZapfChanceC.eot"); /* IE9 Compat Modes */
  src: url("/fonts/ofontruZapfChanceC.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("/fonts/ofontruZapfChanceC.svg") format("svg"), /* Legacy iOS */
    url("/fonts/ofontruZapfChanceC.ttf") format("truetype"), /* Safari, Android, iOS */
    url("/fonts/ofontruZapfChanceC.woff") format("woff"), /* Modern Browsers */
    url("/fonts/ofontruZapfChanceC.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

